import PropTypes from "prop-types";
// @mui
import { styled, useTheme } from "@mui/material/styles";
import {
  Box,
  Card,
  Grid,
  Table,
  Divider,
  TableRow,
  TableBody,
  TableHead,
  TableCell,
  Typography,
  TableContainer,
} from "@mui/material";
// utils
import { fDateTime } from "../../../../utils/formatTime";
import { fCurrency, fPercent } from "../../../../utils/formatNumber";
// components
import Label from "../../../../components/Label";
import Image from "../../../../components/Image";
import Scrollbar from "../../../../components/Scrollbar";
//
import { useTranslation } from "react-i18next";
import useAuth from "../../../../hooks/useAuth";
import RemesasReceipt from "./RemesasReceipt";

// ----------------------------------------------------------------------

const RowResultStyle = styled(TableRow)(({ theme }) => ({
  "& td": {
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
}));

TicketDetails.propTypes = {
  ticket: PropTypes.object.isRequired,
};

export default function TicketDetails({ ticket }) {
  const theme = useTheme();
  const { t } = useTranslation();
  const { user } = useAuth();

  if (!ticket) {
    return null;
  }
  const BAD_STATUSES = ["I", "A", "FAILED", "REJECTED"];
  const STATUS_LABELS = {
    FAILED: "Failed",
    REJECTED: "Rejected",
    I: "Incidence",
    A: "Canceled",
  };

  if (BAD_STATUSES.includes(ticket?.transaction_status)) {
    const label =
      STATUS_LABELS[ticket?.transaction_status] || ticket?.transaction_status;

    return (
      <Card sx={{ p: 5, textAlign: "center" }}>
        <Typography variant="h6" color="error" sx={{ mb: 1 }}>
          {t("field.status")} : {label}
        </Typography>

        <Typography variant="body2" sx={{ color: "text.secondary" }}>
          {t("transaction_not_available")}
        </Typography>
      </Card>
    );
  }

  if (ticket?.is_remesas) {
    return <RemesasReceipt ticket={ticket} />;
  }

  return (
    <>
      <Card sx={{ pt: 5, px: 5 }}>
        <Grid container>
          <Grid item xs={12} sm={6}>
            <Image
              disabledEffect
              visibleByDefault
              src="/static/logo/logo.png"
              alt="logo"
              sx={{ maxWidth: 120 }}
            />
          </Grid>

          <Grid item xs={12} sm={6}>
            <Box sx={{ textAlign: { sm: "right" } }}>
              <Typography variant="h6">{ticket?.ref_code}</Typography>
              <Typography variant="h6">ID: {ticket?.transaction_id}</Typography>
            </Box>
          </Grid>

          <Grid item xs={12} sm={3}>
            <Typography
              paragraph
              variant="overline"
              sx={{ color: "text.disabled" }}
            >
              {t("sideBar.vendor")}
            </Typography>
            <Typography variant="body2">{ticket?.full_name || "-"}</Typography>
          </Grid>

          <Grid item xs={12} sm={3}>
            <Typography
              paragraph
              variant="overline"
              sx={{ color: "text.disabled" }}
            >
              {t("field.date")}
            </Typography>
            <Typography variant="body2">
              {fDateTime(ticket?.created_on)}
            </Typography>
          </Grid>
        </Grid>

        <Scrollbar>
          <TableContainer sx={{ minWidth: 960 }}>
            <Table>
              <TableBody>
                <RowResultStyle>
                  <TableCell colSpan={5} />
                  <TableCell align="right">
                    <Box sx={{ mt: 2 }} />
                    <Typography>{t("field.operator")}</Typography>
                  </TableCell>
                  <TableCell align="right" width={120}>
                    <Box sx={{ mt: 2 }} />
                    <Typography>{ticket?.operator_name}</Typography>
                  </TableCell>
                </RowResultStyle>

                <RowResultStyle>
                  <TableCell colSpan={5} />
                  <TableCell align="right">
                    <Typography>{t("field.phone")}</Typography>
                  </TableCell>
                  <TableCell align="right" width={200}>
                    <Typography>{ticket?.phone}</Typography>
                  </TableCell>
                </RowResultStyle>

                {ticket?.pin_number && (
                  <RowResultStyle>
                    <TableCell colSpan={5} />
                    <TableCell align="right">
                      <Typography variant="h6">
                        {t("field.pin_number")}
                      </Typography>
                    </TableCell>
                    <TableCell align="right" width={140}>
                      <Typography>{ticket.pin_number}</Typography>
                    </TableCell>
                  </RowResultStyle>
                )}

                {ticket?.control_number && (
                  <RowResultStyle>
                    <TableCell colSpan={5} />
                    <TableCell align="right">
                      <Typography variant="h6">
                        {t("field.control_number")}
                      </Typography>
                    </TableCell>
                    <TableCell align="right" width={140}>
                      <Typography>{ticket.control_number}</Typography>
                    </TableCell>
                  </RowResultStyle>
                )}

                {ticket?.expiration_date && (
                  <RowResultStyle>
                    <TableCell colSpan={5} />
                    <TableCell align="right">
                      <Typography variant="h6">
                        {t("field.expiration_date")}
                      </Typography>
                    </TableCell>
                    <TableCell align="right" width={140}>
                      <Typography>
                        {fDateTime(ticket.expiration_date)}
                      </Typography>
                    </TableCell>
                  </RowResultStyle>
                )}

                <RowResultStyle>
                  <TableCell colSpan={5} />
                  <TableCell align="right">
                    <Typography variant="h6">{t("field.montant")}</Typography>
                  </TableCell>
                  <TableCell align="right" width={140}>
                    <Typography>
                      {fCurrency(ticket?.amount)} {ticket?.currency}
                    </Typography>
                  </TableCell>
                </RowResultStyle>

                <RowResultStyle>
                  <TableCell colSpan={5} />
                  <TableCell align="right">
                    <Typography variant="h6">
                      {t("field.delivered_amount")}
                    </Typography>
                  </TableCell>
                  <TableCell align="right" width={140}>
                    <Typography>
                      {fCurrency(ticket?.delivered_amount)}{" "}
                      {ticket?.delivered_amount_currency_code}
                    </Typography>
                  </TableCell>
                </RowResultStyle>

                <RowResultStyle>
                  <TableCell colSpan={5} />
                  <TableCell align="right">
                    <Typography variant="h6">
                      {t("field.save_percent")}
                    </Typography>
                  </TableCell>
                  <TableCell align="right" width={140}>
                    <Typography>
                      {fCurrency(ticket?.save_percent)}{" "}
                      {ticket?.delivered_amount_currency_code}
                    </Typography>
                  </TableCell>
                </RowResultStyle>

                <RowResultStyle>
                  <TableCell colSpan={5} />
                  <TableCell align="right">
                    <Typography variant="h6">
                      {t("field.fee_amount")}
                    </Typography>
                  </TableCell>
                  <TableCell align="right" width={140}>
                    <Typography>
                      {fCurrency(ticket?.fee)} {ticket?.currency}
                    </Typography>
                  </TableCell>
                </RowResultStyle>

                <RowResultStyle>
                  <TableCell colSpan={5} />
                  <TableCell align="right">
                    <Typography variant="h6">
                      {t("field.commission")}
                    </Typography>
                  </TableCell>
                  <TableCell align="right" width={140}>
                    <Typography>{fPercent(ticket?.percent)}</Typography>
                  </TableCell>
                </RowResultStyle>

                <RowResultStyle>
                  <TableCell colSpan={5} />
                  <TableCell align="right">
                    <Typography variant="h6">
                      {t("field.commission_sp")}
                    </Typography>
                  </TableCell>
                  <TableCell align="right" width={140}>
                    <Typography>{fPercent(ticket?.sp_percent)}</Typography>
                  </TableCell>
                </RowResultStyle>
              </TableBody>
            </Table>
          </TableContainer>
        </Scrollbar>

        <Divider sx={{ mt: 5 }} />

        <Grid container>
          {/* <Grid item xs={12} md={9} sx={{ py: 3 }}>
            <Typography variant="subtitle2">{t("params.notes")}</Typography>
            <Typography variant="body2">
              {t("params.notices_report")}
            </Typography>
          </Grid> */}
          <Grid item xs={12} md={3} sx={{ py: 3, textAlign: "left" }}>
            <Typography variant="subtitle2">
              {" "}
              {t("params.need_help")}
            </Typography>
            <Typography variant="body2">info@blingtopupxpress.com</Typography>
          </Grid>
        </Grid>
      </Card>
    </>
  );
}
