import { useEffect, useState } from "react";
import { useSnackbar } from "notistack";
import { useParams, useNavigate } from "react-router-dom";
// @mui
import { Container } from "@mui/material";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
// hooks
import useSettings from "../../hooks/useSettings";
// components
import Page from "../../components/Page";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
import { getTicket } from "../../redux/slices/ticket";
// sections
import Ticket from "../../sections/@dashboard/ticket/details";
import extractErrorMessage from "../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

export default function TicketDetails() {
  const { themeStretch } = useSettings();
  const { t } = useTranslation();
  const navigate = useNavigate();

  const { id } = useParams();
  const { enqueueSnackbar } = useSnackbar();

  const [currentTicket, setCurrentTicket] = useState(null);

  useEffect(() => {
    handleGetObject();
  }, [id]);

  const handleGetObject = async () => {
    try {
      const result = await getTicket(id);
      setCurrentTicket(result);
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(errorMessage, {
        variant: "error",
      });
      navigate(PATH_DASHBOARD.ticket.sale);
    }
  };

  return (
    <Page title={`Ticket: ${t("p_ticket.ticket_detail")}`}>
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={t("p_ticket.ticket_detail")}
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("p_ticket.ticket"),
              href: PATH_DASHBOARD.ticket.root,
            },
            { name: currentTicket?.full_name || "" },
          ]}
        />

        <Ticket ticket={currentTicket} />
      </Container>
    </Page>
  );
}
